@font-face {
  font-weight: 500;
  font-family: "Arial";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/arial.woff2") format("woff2"), url("../fonts/arial.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  outline: none;
}
html {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
@mixin list {
  list-style: none;
  margin: 0;
  padding: 0;
}
@mixin reset {
  margin: 0;
  padding: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  position: relative;
  padding: 30px 30px 20px;
}
.header__nav {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 3fr;
}
.header__logo {
  font-size: 24px;
  font-family: "Lusitana", "Arial", sans-serif;
  line-height: 24px;
  font-weight: 700;
  width: 100px;
  letter-spacing: 0.04em;
  padding: 10px 0;
}
.main-nav__toggle {
  position: relative;
  display: flex;
  justify-content: flex-end;
  width: 100%;
  letter-spacing: 0.04em;
  transition-duration: 0.8s;
  color: black;
  padding: 10px 0;
}
.dropdown-menu__button {
  font-family: "Lusitana", "Arial", sans-serif;
  position: absolute;
  display: flex;
  justify-content: flex-end;
  font-size: 24px;
  line-height: 20px;
  transition-duration: 0.8s;
}
.dropdown-menu__button--open {
  right: 0;
  opacity: 1;
}
.dropdown-menu__button--close {
  right: 30%;
  opacity: 0;
}
.dropdown-menu__button--open1 {
  right: 0;
  opacity: 1;
}
.dropdown-menu__button--close1 {
  right: 50%;
  opacity: 0;
}
.header__wrap-list {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 3;
  display: none;
  opacity: 0;
  transition: display 0.3s;
}
.header__list {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding-bottom: 10px;
  border-bottom: 1.5px solid black;
}
.header__list--anim {
  animation-name: lift-up;
  animation-duration: 0.5s;
}
@keyframes lift-up {
  from {
    transform: translateX(120%);
  }
  100% {
    transform: translateX(0);
  }
}
.header__list--open {
  display: grid;
  transform: translate3d(0%);
  opacity: 1;
}
.header__item {
  padding: 10px 0;
}
.header__link {
  font-size: 24px;
  line-height: 24px;
  font-family: "Lusitana", "Arial", sans-serif;
  transition-duration: 0.8s;
}
.intro,
.skills {
  padding: 0 30px 30px;
}
.intro__title {
  position: relative;
  font-family: "Lusitana", "Arial", sans-serif;
  font-size: 50px;
  line-height: 65px;
  text-transform: uppercase;
  margin-bottom: 30px;
  text-align: center;
  border-bottom: 1.5px solid black;
  padding: 30px 0;
  letter-spacing: 0.04em;
}
.intro__desc {
  font-family: "Lusitana", "Arial", sans-serif;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: 0.04em;
}
.intro__photo {
  width: 100%;
  margin-bottom: 20px;
}
.skills__title {
  font-size: 24px;
  font-family: "Lusitana", "Arial", sans-serif;
  line-height: 24px;
  font-weight: 700;
  letter-spacing: 0.04em;
  margin-bottom: 15px;
}
.skills__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.skills__item {
  padding: 5px 0;
}
.skills__desc {
  font-family: "Lusitana", "Arial", sans-serif;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: 0.04em;
}
.header__button {
  grid-column-start: 2;
  text-align: end;
  font-size: 24px;
  line-height: 24px;
  font-family: "Lusitana", "Arial", sans-serif;
  transition-duration: 0.8s;
  color: black;
  padding: 10px 0;
}
.index-page {
  position: relative;
  min-width: 320px;
  font-family: "Lusitana", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-style: normal;
  color: black;
  background-color: white;
}
.dark {
  color: white;
  background-color: #0f0f0f;
  transition-duration: 0.8s;
}
.dark__button {
  color: white;
  transition-duration: 0.8s;
}
.dark__border {
  border-bottom: 1.5px solid white;
}
